___CSS_LOADER_EXPORT___.push([module.id, `.app {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  padding: 1rem;
  max-width: 1400px;
  margin: 0 auto;
}

.app-header {
  text-align: center;
  padding: 1rem 0 1.5rem;
}

.app-header h1 {
  font-size: 2rem;
  color: var(--color-primary-light);
  margin-bottom: 0.25rem;
}

.app-header .subtitle {
  color: var(--color-text-muted);
  font-size: 0.9rem;
}

.app-main {
  display: flex;
  gap: 1.5rem;
  flex: 1;
}

.simulation-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  min-width: 0;
}

.sidebar {
  width: 280px;
  flex-shrink: 0;
}

.app-footer {
  text-align: center;
  padding: 1rem 0;
  color: var(--color-text-muted);
  font-size: 0.85rem;
}

/* Responsive */
@media (max-width: 900px) {
  .app-main {
    flex-direction: column;
  }

  .sidebar {
    width: 100%;
  }
}

@media (max-width: 600px) {
  .app {
    padding: 0.5rem;
  }

  .app-header h1 {
    font-size: 1.5rem;
  }

  .app-main {
    gap: 1rem;
  }
}
