___CSS_LOADER_EXPORT___.push([module.id, `.status-bar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: rgba(30, 30, 40, 0.9);
  padding: 0.75rem 1rem;
  border-radius: 6px;
  margin-top: 0.5rem;
  font-family: monospace;
  font-size: 0.85rem;
}

.status-text {
  color: #94a3b8;
}

.status-badge {
  padding: 0.25rem 0.5rem;
  border-radius: 4px;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
}

.status-badge.paused {
  background: #f59e0b;
  color: #000;
}
