___CSS_LOADER_EXPORT___.push([module.id, `.file-uploader {
  background: rgba(30, 30, 40, 0.9);
  border-radius: 8px;
  padding: 1.5rem;
  margin-top: 1rem;
}

.file-uploader h3 {
  margin: 0 0 1rem 0;
  color: #a78bfa;
  font-size: 1.25rem;
  border-bottom: 1px solid #4c4c5c;
  padding-bottom: 0.5rem;
}

.file-input-hidden {
  display: none;
}

.upload-button {
  width: 100%;
  padding: 0.75rem 1rem;
  border: 2px dashed #4c4c5c;
  border-radius: 6px;
  background: transparent;
  color: #e2e8f0;
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.2s ease;
}

.upload-button:hover {
  border-color: #7c3aed;
  background: rgba(124, 58, 237, 0.1);
}

.upload-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.status-message {
  margin-top: 0.75rem;
  padding: 0.5rem 0.75rem;
  border-radius: 4px;
  font-size: 0.85rem;
}

.status-message.success {
  background: rgba(16, 185, 129, 0.2);
  color: #10b981;
}

.status-message.error {
  background: rgba(239, 68, 68, 0.2);
  color: #ef4444;
}

.status-message.info {
  background: rgba(59, 130, 246, 0.2);
  color: #3b82f6;
}

.file-hint {
  margin: 0.75rem 0 0 0;
  font-size: 0.75rem;
  color: #64748b;
}
